<template>
  <v-container fluid>
    <v-toolbar flat>
      <v-btn @click="parsePom" :loading="loading">Read pom info from digest</v-btn>
      <v-spacer></v-spacer>
      <v-switch v-model="showEmptyPhase" label="show empty phases"/>
    </v-toolbar>
    <v-subheader>Loaded {{projects.length}} Projects</v-subheader>
    <v-expansion-panels>
      <Project :show-empty-phase="showEmptyPhase" :project="project" v-for="project in projects"
               :key="`${project.meta.groupId}:${project.meta.artifactId}`"/>
    </v-expansion-panels>
  </v-container>
</template>

<script>
  import Project from "./Project"
  import axios from 'axios'

  export default {
    name: 'PomVisualizer',
    components: {Project},
    data: () => ({
      projects: [],
      showEmptyPhase: false,
      loading: false,
    }),
    methods: {
      parsePom() {
        this.loading = true
        axios.get("buildplan.jsonl").then(response => {
          const lines = response.data.split("\n").filter(l => !!l)
          const projects = []
          let currentProject
          lines.forEach(line => {
            const obj = JSON.parse(line)
            if (obj.type === "project") {
              currentProject = {
                meta: obj,
                phases: []
              }
              projects.push(currentProject)
            } else if (obj.type === "execution") {
              currentProject.phases.push(obj)
            }
          })
          this.projects = projects
        }).finally(() => this.loading = false)
      },
    },
    mounted() {
      this.parsePom()
    }
  }
</script>
