<template>
  <v-timeline dense>
    <v-timeline-item v-for="(phase, i) in showEmptyPhase ? phases : phases.filter(p => !!mapping[p])" :key="i"
    :color="mapping[phase] ? 'primary' : 'grey'" small>
      <v-card>
        <v-card-subtitle class="blue lighten-5">
          {{phase}} Phase ({{mapping[phase] ? mapping[phase].length : '0'}} executions)
        </v-card-subtitle>
        <v-card-text>
          <v-expansion-panels accordion multiple flat>
            <v-expansion-panel v-for="(execution, j) in mapping[phase] || []" :key="`${j}-execution`" class="my-1">
              <v-expansion-panel-header class="green lighten-2 white--text">
                <span><strong>ID:&nbsp;</strong>{{execution.executionId}}</span>
                <span><strong>Goal:&nbsp;</strong>{{execution.goal}}</span>
                <span><strong>Plugin:&nbsp;</strong>{{execution.pluginGroupId}} : {{execution.pluginArtifactId}} : {{execution.pluginVersion}}</span>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-card outlined>
                  <v-card-text>
                    <v-subheader>
                      Config
                    </v-subheader>
                    <pre v-html="renderConfig(execution.configXml)"/>
                  </v-card-text>
                </v-card>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-card-text>
      </v-card>
    </v-timeline-item>
  </v-timeline>
</template>

<script>
  import hljs from 'highlight.js'
  import 'highlight.js/styles/github.css'

  export default {
    name: "Lifecycle",
    props: ['phases', 'mapping', 'showEmptyPhase'],
    methods: {
      renderConfig(config) {
        if (!config) {
          return ""
        }
        return hljs.highlightAuto(config).value
      }
    }
  }
</script>

