<template>
  <v-expansion-panel>
    <v-expansion-panel-header>{{groupId}} : {{artifactId}} : {{version}}</v-expansion-panel-header>
    <v-expansion-panel-content>
      <lifecycle :show-empty-phase="showEmptyPhase" :phases="phases" :mapping="mapping"/>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>

<script>
  import Lifecycle from "./Lifecycle"
  import _ from 'lodash'

  export default {
    name: "Project",
    components: {Lifecycle},
    props: ['project', 'showEmptyPhase'],
    data: function () {
      const mapping = _.groupBy(this.project.phases, 'phase')
      return {
        tab: 'tab-default',
        artifactId: this.project.meta.artifactId,
        groupId: this.project.meta.groupId,
        version: this.project.meta.version,
        mapping: mapping,
        phases: [
          'validate',
          'initialize',
          'generate-sources',
          'process-sources',
          'generate-resources',
          'process-resources',
          'compile',
          'process-classes',
          'generate-test-sources',
          'process-test-sources',
          'generate-test-resources',
          'process-test-resources',
          'test-compile',
          'process-test-classes',
          'test',
          'prepare-package',
          'package',
          'pre-integration-test',
          'integration-test',
          'post-integration-test',
          'verify',
          'install',
          'deploy',
        ],
      }
    },
  }
</script>

